import { useEffect, useState } from "react";
import { getLeaderboard } from "../lib/api";

export default function Leaderboard() {
  const [scores, setScores] = useState([]);

  useEffect(() => {
    getLeaderboard().then(setScores).catch(console.error);
  }, []);

  return (
    <div className="min-h-screen p-6 text-center">
      <h1 className="text-3xl font-bold mb-6">üèÜ Leaderboard</h1>

      <div className="max-w-md mx-auto bg-gray-800 p-4 rounded-lg">
        {scores.length === 0 ? (
          <p>No scores yet‚Ä¶ Play a game!</p>
        ) : (
          scores.map((score, i) => (
            <div
              key={i}
              className="flex justify-between py-2 border-b border-gray-600"
            >
              <span>{score.name}</span>
              <span>{score.moves} moves</span>
              <span>{score.time}s</span>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
